<template>
  <div class="ratings" ref="ratings">
   <div class="ratings-content">
   	<!--  -->
   	<div class="overview">
   		<div class="overview-left">
   			<h1 class="score">{{seller.score}}</h1>
   			<div class="title"></div>
   			<div class="rank">{{seller.rankRate}}%</div>
   		</div>
   		<div class="overview-right">
   			<div class="score-wrapper">
   				<span class="title"></span>
   				<star :size="36" :score="seller.serviceScore"></star>
   				<span class="score" >{{seller.serviceScore}}</span>
   			</div>
   			<div class="score-wrapper">
   				<span class="title"></span>
   				<star :size="36" :score="seller.foodScore"></star>
   				<span class="score" >{{seller.foodScore}}</span>
   			</div>
   			<div class="delivery-wrapper">
   				<span class="title"></span>
   				<span class="delivery" >{{seller.deliveryTime}}</span>
   			</div>
   		</div>
   	</div>
	<split></split>
	<!--  -->
	<ratingselect @select="selectRating" @toggleContent="toggleContent" :selectType="selectType" :onlyContent="onlyContent" :ratings="ratings"></ratingselect>
	<div class="rating-wrapper">
		<ul>
			<li class="rating-item" v-for="rating in ratings" v-show="needShow(rating.rateType,rating.text)" >
				<div class="avatar">
					<img :src="rating.avatar" width="28" height="28">
				</div>
				<div class="content">
					<h1 class="name">{{rating.username}}</h1>
					<div class="star-wrapper">
						<star :size="24" :score="rating.score"></star>
						<span class="delivery" v-show="rating.deliveryTime">{{rating.deliveryTime}}</span>
					</div>
					<p class="text">{{rating.text}}</p>
					<div class="recommend" v-show="rating.recommend &&rating.recommend.length">
						<span class="icon-thumb_up"></span>
						<span class="item" v-for="item in rating.recommend">{{item}}</span>
					</div>
					<div class="time">
						{{rating.rateTime|formatDate}}
					</div>
				</div>
			</li>
		</ul>
	</div>
   </div>
  </div>
</template>

<script type="text/ecmascript-6">
import {formatDate} from '../../common/js/date';
import star from '../star/star'
import split from '../split/split'
import ratingselect from '../ratingselect/ratingselect'
import BScroll from 'better-scroll'

const ERRNO=0
const ALL=2
export default {
  data () {
    return {
    	ratings:[],
    	selectType:ALL,
    	onlyContent:true
    }
  },
  props:{
  	seller:{
  		type:Object,
  		default:{}
  	},

  },
  components:{
  	star,
  	split,
  	ratingselect
  },
  created(){
  	this.$http.get('api/ratings').then(function(res){
 	console.log(res)
      res=res.body  
      if(res.errno===ERRNO){
        this.ratings=res.data;
        this.$nextTick(()=>{
  			this.scroll=new BScroll(this.$refs.ratings,{click:true})
  		})
      }
    }) 	
  },
  methods:{
  	needShow(type,text){
  		if(this.onlyContent&&!text){
  			return false
  		}
  		if(this.selectType===ALL){
  			return true
  		}else{
  			return type===this.selectType
  		}
  	},
  	selectRating(type){
  		this.selectType=type;
  		this.$nextTick(()=>{
  			this.scroll.refresh()
  		})
  	},
  	toggleContent(){
  		this.onlyContent=!this.onlyContent;
  		this.$nextTick(()=>{
  			this.scroll.refresh()
  		})
  	}
  },
  filters:{
  	formatDate(time){
  		let date=new Date(time)
  		return formatDate(date)
  	}  
  }
}
</script>


<style lang="stylus" rel="stylesheet/stylus">
@import '../../common/stylus/mixin.styl'
.ratings
	position:absolute
	left:0
	top:174px
	bottom:0
	width:100%
	overflow:hidden
	.overview
		display:flex
		padding:18px 0
		.overview-left
			flex:0 0  137px
			padding:6px 0
			width:137px
			border-right:1px solid rgba(7,17,27,0.1)
			text-align:center
			.score
				margin-bottom:6px
				font-size:24px
				color:rgb(255,153,0)
				line-height:28px
			.title
				margin-bottom:8px
				line-height:12px
				font-size:12px
				color:rgb(7,17,27)
			.rank
				line-height:10px
				font-size:10px
				colorg:rgb(147,153,159)
		.overview-right
			flex:1
			padding-left:24px
			.score-wrapper
				margin-bottom:8px
				font-size:0
				.title
					display:inline-block
					line-height:18px
					vertical-align:top
					font-size:12px
					color:rgb(7,17,27)
				.star
					display:inline-block
					margin:0 12px
					vertical-align:top
				.score
					display:inline-block
					line-height:18px
					vertical-align:top
					color:rgb(255,153,0)
			.delivery-wrapper
				font-size:0
				.title
					font-size:18px
					line-height:12px
					color:rgb(147,153,159)
				.delivery
					margin-left:12px
					font-size:12px
					color:rgb(147,153,159)	
	.rating-wrapper
		padding:0 18px
		.rating-item
			display:flex
			padding:18px 0
			border-1px(rgba(7,17,27,0.1))
			.avatar
				flex: 0 0 28px
				width:28px
				margin-right:12px
				img
					border-radius:50%
			.content
				flex:1
				position:relative
				.name
					margin-bottom:4px
					line-height:12px
					font-size:10px
					color:rgb(7,17,27)
				.star-wrapper
					margin-bottom:6px
					font-size:0
					.star
						display:inline-block
						margin-right:6px
						vertical-align:top
					.delivery
						display:inline-block
						vertical-align:top
						line-height:12px
						font-size:10px
						color:rgb(147,153,159)
				.text
					margin-bottom:8px
					line-height:18px
					color:rgb(7,17,27)
					font-size:12px
				.recommend
					line-height:16px
					font-size:0
					.icon-thumb_up,.item
						display:inline-block
						margin:0 8px 4px 0
						font-size:9px
					.icon-thumb_up
						color:rgb(0,160,220)
					.item
						padding:0 6px
						border:1px solid rgba(7,17,27,0.1)
						border-radius:1px
						color:rgb(147,153,159)
						background:#fff
				.time
					position:absolute
					top:0
					right:0
					line-height:12px
					font-size:10px
					color:rgb(147,153,159)


</style>



